"use client";
import { useState, useEffect } from "react";
import Cookies from "js-cookie";

export default function DataTable({ columns, data, onEdit, onDelete }) {
  const [sortConfig, setSortConfig] = useState({ key: null, direction: "asc" });

  // Ordena√ß√£o dos dados
  const sortedData = [...data].sort((a, b) => {
    if (!sortConfig.key) return 0;

    const valueA = a[sortConfig.key];
    const valueB = b[sortConfig.key];

    if (typeof valueA === "number" && typeof valueB === "number") {
      return sortConfig.direction === "asc" ? valueA - valueB : valueB - valueA;
    } else {
      return sortConfig.direction === "asc"
        ? String(valueA).localeCompare(String(valueB))
        : String(valueB).localeCompare(String(valueA));
    }
  });

  // Alternar ordena√ß√£o
  const handleSort = (key) => {
    setSortConfig((prev) => ({
      key,
      direction: prev.key === key && prev.direction === "asc" ? "desc" : "asc",
    }));
  };

  return (
    <div className="bg-white shadow-md rounded-lg overflow-hidden mt-6">
      {data.length === 0 ? (
        <p className="text-gray-600 text-center p-4">Nenhum registro encontrado.</p>
      ) : (
        <table className="w-full border-collapse">
          <thead className="sticky top-0 bg-gray-800 text-white">
            <tr>
              {columns.map((col) => (
                <th
                  key={col.key}
                  onClick={() => handleSort(col.key)}
                  className="p-4 text-left cursor-pointer hover:bg-gray-700 transition"
                >
                  {col.label} {sortConfig.key === col.key && (sortConfig.direction === "asc" ? "üîº" : "üîΩ")}
                </th>
              ))}
              <th className="p-4 text-center">Editar</th>
              <th className="p-4 text-center">Excluir</th>
            </tr>
          </thead>
          <tbody>
            {sortedData.map((item, index) => (
              <tr
                key={item.codigo}
                className={`border-t ${index % 2 === 0 ? "bg-gray-100" : "bg-white"}`}
              >
                {columns.map((col) => (
                  <td key={col.key} className="p-4">
                    {col.render ? col.render(item) : item[col.key]}
                  </td>
                ))}
                <td className="p-4 text-center">
                  <button
                    onClick={() => onEdit(item)}
                    className="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600 transition-all"
                  >
                    ‚úèÔ∏è Editar
                  </button>
                </td>
                <td className="p-4 text-center">
                  <button
                    onClick={() => onDelete(item)}
                    className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600 transition-all"
                  >
                    üóëÔ∏è Excluir
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
